.menuContainer {
  width: 100%;
  transition: 1s;
}
.menuHeaderWrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 15vw;
}

.header_menu{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.header_menu_items{
  display: flex;
align-items: center;
justify-content: center;
gap: 5vw;
color: #FFFBFE;
line-height: 27px;
font-weight: 600;
backdrop-filter: blur(1px);
font-size:calc(var(--index)*.9);
cursor: pointer;
}

#hamburger_menu {
  height: 10vh;
  width: 50px;
  position: relative;
  transition-duration: 1s;
  cursor: pointer;
  /* top: 5vh; */
  /* left: 9vw; */
  visibility: visible;
  display: none;
}
#hamburger_menu span {
  height: 5px;
  width: 40px;
  background-color: #FFFBFE;
  border-radius: 20px;
  position: absolute;
  top: calc(var(--index) * 2.5);
  transition-duration: 0.25s;
  transition-delay: 0.25s;
}
#hamburger_menu span:before {
  left: 0;
  position: absolute;
  top: -10px;
  height: 5px;
  width: 40px;
  background-color: #FFFBFE;
  content: "";
  border-radius: 20px;
  transition-duration: 0.25s;
  transition: transform 0.25s, top 0.25s 0.25s;
}
#hamburger_menu span:after {
  left: 0;
  position: absolute;
  top: 10px;
  height: 5px;
  width: 40px;
  background-color: #FFFBFE;
  content: "";
  border-radius: 20px;
  transition-duration: 0.25s;
  transition: transform 0.25s, top 0.25s 0.25s;
}
#hamburger_menu.open span {
  transition-duration: 0.1s;
  transition-delay: 0.3s;
  background: transparent;
}
#hamburger_menu.open span:before {
  transition: top 0.25s, transform 0.25s 0.25s;
  transform: rotateZ(-45deg);
  top: 0;
}
#hamburger_menu.open span:after {
  transition: top 0.4s, transform 0.25s 0.25s;
  transform: rotateZ(45deg);
  top: 0;
}
.menuItemsWrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 72vh;
}
.menu{
box-shadow: 3px 3px 5px gray;
backdrop-filter: blur(10px);
border-radius: 12px;
}
.menuItems_items {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 3vw;
  color: #FFFBFE;
  font-size: calc(var(--index)*1.4);
  padding: 10vh 12vw;
}
.menuItems_items li:hover {
  cursor: pointer;
  filter: drop-shadow(3px 3px 7px #FFFBFE)
    drop-shadow(3px 3px 15px rgb(245, 216, 56))
    drop-shadow(3px 3px 27px rgb(245, 226, 122));
}
.visibility {
  display: none;
}

@media screen and ( max-width:1280px ) {
  .header_menu_items{
    display: none;
  }
  #hamburger_menu {
    display: block;
  }
}
