.titlePage{
    height: 100vh;
}
.header{
    min-height: 14.14vh;
    padding: 2.36vh 8.8vw;
}

li{
    list-style: none;
}
.header_wrapper{
display: flex;
align-items: center;
justify-content: space-between;
margin-bottom: 10vh;
}
.titlePage-container{
    margin: 10vh auto ;
}
.titlePage_content{
    text-align: center;
backdrop-filter: blur(2px);
}
.bigLogo-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.bigLogo{
    width: calc(var(--index)*30);
    text-align: center;
}
text{
    color: #FFFBFE;
font-weight: 700;
line-height: 67.2px;
font-size: 1.4vh;
}

.companySkills_items{
    color: #FFFBFE;
    width: fit-content;
    display: flex;
    flex-wrap: nowrap;
align-items: center;
justify-content: space-evenly;
gap: 4vw;
font-weight: 500;
font-size: 24px;
    line-height: 36px;
    white-space: nowrap;
    cursor: pointer;
    margin: 15vh auto 0;
}

 .titlePage_content_name_subtitle{
    color: #FFFBFE;
    font-size: 5vh;
 }

 @media screen and ( max-width:520px ) {
   .bigLogo{
    width: calc(var(--index)*20);
   }
  }