.cardsArray{
    display:flex;
}
.textCard{
    width:50%;
}
.cardAbout{
margin: 17vh auto;
text-align: center;
    height:70vh;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    width:40vw;
    font-size:3rem;
    font-weight:900;
    color: antiquewhite;
    text-shadow: 3px 3px 3px rgb(204, 158, 158);
}
.cardAbout p{
    font-weight:500;
    text-shadow: 3px 3px 3px rgb(117, 112, 112);
}
.imagesContainer{
    width:50%;
    height:100vh;
    display:flex;
    flex-direction:column;
    justify-content:center;
}
.imagesWrapper{
    width:45vw;
    height:80vh;
    position:relative;
}
.imageScroll{
    position:absolute;
        width:100%;
        height:100%;
    }
img{
    height:100%;
    width:100%;
    border-radius: 12px;
    filter: drop-shadow(0 0 7px grey);
}